.header {
	padding: 20px 0 29px;
	background: $white;
	@include lg {
		padding: 20px 0; }
	@include sm {
		z-index: 100; }

	&__container {
		position: relative;
		display: flex;
		align-items: center;
		z-index: 2;
		@include sm {
			flex-direction: column;
			justify-content: flex-start; } }
	&__block {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		flex: 1 1 100%;
		margin-left: 35px;
		padding-left: 35px;
		border-left: 1px solid #e8e9ed;
		@include md {
			margin-left: 18px;
			padding-left: 16px; }
		@include sm {
			position: relative;
			padding-left: 0;
			margin: 5px 0 0;
			border-left: 0;
			width: 100%; } }
	&__contact-info {
		flex: 1 1 100%;
		padding: 10px 0;
		@include md {
			flex: 1 1 80%;
			order: 1; } }
	&__nav {
		display: inline-flex;
		flex: 1 1 auto;
		padding: 15px 0 0;
		margin-right: 15px;
		@include md {
			order: 3;
			margin-right: 0;
			padding: 10px 0 0; } }
	&__action-bar {
		display: inline-flex;
		flex: 0 0 auto;
		@include md {
			flex-direction: column;
			order: 2; }
		@include sm {
			position: absolute;
			right: 0;
			bottom: 15px;
			flex-direction: row; } }
	&__logo {
		position: relative;
		margin-top: 10px;
		&.active.logo_color::after {
			opacity: 1; }
		&.logo_color::after {
			content: '';
			@include s('logo');
			position: absolute;
			top: 0;
			left: 0;
			opacity: 0;
			transition: .3s; }
		@include sm {
			flex: 0 0 auto;
			align-self: flex-start;
			z-index: 300;
			position: relative; } }
	h1 {
		position: absolute;
		clip: rect(0, 0, 0, 0);
		height: 1px;
		width: 1px;
		margin: -1px;
		@include hide-text; }

	&__nav-toggle {
		@include sm {
			display: block; } }

	&_home {
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		padding: 52px 0 10px;
		background: transparent;
		@include lg {
			padding: 25px 0 10px; }
		@include sm {
			padding: 10px 0; }
		.contact-info__link-text,
		.nav__list-link {
			color: $white; }
		.contact-info__link-icon {
			fill: #b3fcff; }
		.nav__list-item.is-active {
			border-bottom-color: transparentize(#85c8cf, .5); }
		.action-bar__btn {
			background: $white; }
		.action-bar__btn-icon {
			fill: $blue-dark; }
		.header__block {
			border-left-color: transparentize($white, .8); }
		.nav-toggle .line {
			background: #fff; } } }


/* nav
	----------------------------------- */
.nav {
	@include sm {
		display: none;
		position: fixed;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		background-image: linear-gradient(45deg, #01be9d 0%, #0057b8 100%);
		z-index: 101;
		padding: 0; }
	&__list {
		display: flex;
		flex: 0 1 764px;
		justify-content: space-between;
		@include sm {
			flex-direction: column;
			justify-content: center;
			align-items: stretch;
			text-align: center;
			overflow: auto; }
		&-item {
			padding-bottom: 9px;
			border-bottom: 1px solid transparent;
			line-height: 1;
			@include sm {
				border-bottom: 0;
				padding: 0; }
			&.is-active {
				border-bottom-color: #dcdcdc;
				@include sm {
					border-bottom-color: transparent; } }
			&.is-active a {
				@include sm {
					color: $blue-dark; } }
			&:last-child {
				@include md {
					display: none; }
				@include sm {
					display: block; } } }
		&-link {
			@include f('pn-600', 14);
			text-transform: uppercase;
			color: $blue-dark;
			@include md {
				font-size: 13px; }
			@include sm {
				display: block;
				padding: 20px;
				font-size: 18px;
				color: $white; } } } }

/* contact-info
	----------------------------------- */
.contact-info {
	font-size: 0;
	@include md {
		display: flex;
		flex-wrap: wrap; }
	@include sm {
		flex-direction: column; }
	&__link {
		display: inline-block;
		vertical-align: top;
		font-size: 0;
		white-space: nowrap;
		@include md {
			flex: 1 1 60%;
			padding-right: 10px; }
		> svg {
			@include sm {
				width: 21px!important; } }

		&:first-child {
			margin-right: 10px;
			@include md {
				margin-right: 0; } }
		&:not(:first-child) {
			margin-left: 43px;
			@include lg {
				margin-left: 14px; }
			@include md {
				margin-left: 0; } }
		&:nth-child(2n) {
			flex: 1 1 30%; }
		&-icon {
			display: inline-block;
			vertical-align: middle;
			margin-right: 10px;
			fill: $aqua;
			font-size: 20px;
			&.icon-balloon {
				width: 14px;
				height: 21px;
				font-size: 21px; }
			&.icon-mail {
					font-size: 16px; }
			&.icon-time {
				width: 21px; } }
		&-text {
			display: inline-block;
			vertical-align: middle;
			font-size: 15px;
			line-height: 1;
			color: $blue-dark; } } }

/* action-bar
	----------------------------------- */
.action-bar {
	&__btn {
		height: 30px;
		width: 30px;
		background: $aqua-dark;
		border-radius: 50%;
		text-align: center;
		line-height: 32px;
		transition: background .3s;
		@include sm {
			height: 35px;
			width: 35px;
			line-height: 40px; }
		&:not(:first-child) {
			margin-left: 5px;
			@include md {
				margin-left: 0;
				margin-top: 5px; }
			@include sm {
				margin-left: 10px;
				margin-top: 0; } }
		@include hover {
			background: $white; }
		.no-touch &:hover &-icon {
			fill: $aqua-dark; }
		&-icon {
			fill: $white;
			transition:  fill .3s;
			&.icon-search {
				font-size: 12px;
				@include sm {
					font-size: 15px; } }
			&.icon-mail {
				position: relative;
				font-size: 11px;
				@include sm {
					font-size: 14px; } } } } }
