.page-info {
	display: flex;
	min-height: 310px;
	@include cover;
	@include md {
		min-height: 250px; }
	&__container {
		display: flex;
		flex-wrap: wrap;
		justify-content: center;
		align-items: flex-start;
		flex: 1 1 auto; }
	&__text {
		flex: 1 1 100%;
		margin-top: -60px;
		text-align: center;
		@include sm {
			margin-top: -40px; }
		&_has-sub-title {
			margin-top: -30px;
			@include sm {
				margin-top: -10px; } } }
	&__text_has-sub-title-big &__sub-title {
		max-width: none;
		font-size: 30px; }
	&__title {
		max-width: 380px;
		margin: 0 auto;
		&_sm {
			max-width: 240px; }
		@include sm {
			font-size: 32px;
			line-height: 32px; } }
	&__sub-title {
		max-width: 620px;
		margin: 20px auto 0;
		font-size: 20px;
		line-height: 30px;
		@include sm {
			margin: 5px auto 0;
			font-size: 17px;
			line-height: 25px; } }
	&__breadcrumbs {
		flex: 1 1 100%;
		margin: 39px 0 0;
		@include sm {
			margin: 25px 0 0; } } }

/* breadcrumbs
	----------------------------------- */
.breadcrumbs {
	display: flex;
	&__item {
		&:not(:first-child) {
			margin-left: 6px; } }
	&__item.is-active &__link {
		pointer-events: none;
		background: $white;
		color: $blue-dark; }
	&__link {
		display: block;
		padding: 0 18px;
		height: 22px;
		line-height: 22px;
		@include f('pn-700', 11);
		text-transform: uppercase;
		box-shadow: 2px 3px 2px rgba(0, 0, 0, 0.1);
		border-radius: 10px;
		border: 1px solid $white;
		color: $white;
		transition: .3s;
		@include hover {
			background: $blue-dark; } } }
