// basic styles
body {
	height: 100%;
	min-width: 320px;
	@include f('pn-400', 16);
	line-height: 30px;
	color: $black;
	&.is-locked {
		position: fixed;
		top: 0;
		left: 0;
		right: 0;
		height: 100%;
		overflow: hidden; } }

* {
	-webkit-tap-highlight-color: transparent; }

h1, h2, h3, h4, h5, h6 {
	font-weight: normal; }

a {
	color: inherit;
	text-decoration: none;
	&:hover, &:focus {
		text-decoration: none; } }

button {
	background-color: transparent; }

h1, h2, h3, h4, h5, h6, p, a, span, div, img, mark, ul, li {
	&::selection {
		color: $white;
		background-color: $aqua; } }

img {
	vertical-align: top;
	max-width: 100%; }

::-ms-clear,
::-ms-reveal {
	display: none;
	width: 0;
	height: 0; }

input:invalid {
	box-shadow: none; }
input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
	-webkit-appearance: none;
	margin: 0; }

.obj-fit {
	width: 100%;
	height: 100%;
	object-fit: cover;
	font-family: 'object-fit: cover;'; }

.out {
	display: flex;
	flex-direction: column;
	min-height: 100vh;
	overflow: hidden; }

.container {
	max-width: 1140px;
	margin: 0 auto;
	padding: 0 15px;
	@include sm {
		padding: 0 10px; } }

.btn {
	display: inline-block;
	padding: 11px 39px;
	box-shadow: 2px 3px 2px rgba(0, 0, 0, 0.1);
	border-radius: 26px;
	text-transform: uppercase;
	@include f('pn-700', 15);
	letter-spacing: 0.05em;
	line-height: 30px;
	transition: background .3s ease-in-out, color .3s ease-in-out, transform .1s ease-in-out, box-shadow .1s ease-in-out;
	&_white {
		background: $white;
		color: $blue-dark;
		@include hover {
			color: $aqua-dark; } }
	&_aqua {
		background: $aqua;
		color: $white;
		@include hover {
			background: $aqua-dark; } }
	&_white-bordered {
		padding: 10px 42px;
		font-size: 15px;
		color: $blue-dark;
		border: 2px solid #00899a;
		background: $white;
		box-shadow: none;
		transition: background .3s ease-in-out, color .3s ease-in-out, transform .1s ease-in-out, border .3s ease-in-out;
		@include hover {
			background: $aqua;
			color: $white;
			border-color: $aqua; } }
	&_file {
		@extend .btn_white-bordered;
		position: relative;
		padding: 10px 21px 10px 61px;
		cursor: pointer;
		border-color: #0f2740;
		letter-spacing: .05em;
		.no-touch &:hover .icon-file {
			fill: $white; }
		.icon-file {
			position: absolute;
			top: calc(50% - 11px);
			left: 26px;
			font-size: 19px;
			transition: .3s ease-in-out; }
		input {
			display: none; } } }

.title-lg {
	@include f('pn-700', 45);
	line-height: 50px;
	text-shadow: 2px 3px 2px rgba(0, 0, 0, 0.1);
	text-transform: uppercase;
	letter-spacing: 0.025em;
	color: $white;
	@include sm {
		font-size: 40px;
		line-height: 40px; } }

.title-sm-white {
	@extend .title-lg;
	font-size: 30px;
	line-height: 35px; }

.title-md {
	@include f('pn-700', 40);
	color: $blue-dark;
	line-height: 50px;
	text-transform: uppercase;
	letter-spacing: 0.02em;
	@include sm {
		font-size: 35px;
		line-height: 35px; } }

.title-sm {
	@extend .title-md;
	font-size: 30px;
	line-height: 35px;
	letter-spacing: 0.025em; }

.input {
	width: 100%;
	padding: 0 30px;
	height: 53px;
	border-radius: 26px;
	border: 2px solid #c9c9c9;
	@include f('pn-400', 15);
	color: $blue-dark;
	@include placeholder-color($blue-dark);
	&_aqua, &_transp {
		padding: 0 34px;
		background: #2da6b2;
		border-color: $white;
		color: $white;
		@include placeholder {
			color: $white;
			font-style: italic; } }
	&_gray {
		@include f('pn-400');
		@include placeholder {
			font-style: normal; } }
	&_transp {
		background: transparent; }
	&_popup {
		height: 45px;
		border-radius: 22px; }
	&_radius {
		border-radius: 20px; } }

.textarea {
	display: block;
	width: 100%;
	padding: 20px;
	resize: none;
	@include f('pn-300', 16);
	line-height: 25px;
	color: $white;
	border-radius: 27px;
	border: 2px solid $white;
	@include placeholder {
		color: $white;
		font-style: italic; }
	&_transp {
		background: transparent; }
	&_popup {
		min-height: 136px;
		padding: 12px 27px;
		border-radius: 27px;
		border: 2px solid #c9c9c9;
		background-color: #ffffff;
		color: $blue-dark;
		@include f('pn-400');
		@include placeholder {
			color: $blue-dark;
			font-style: normal;
			font-size: 15px; } } }
