.services-block {
	&__container {
		position: relative;
		z-index: 5; }
	&__main-title {
		text-align: center;
		margin-bottom: 27px; }
	&__row {
		display: flex;
		@include sm {
			flex-wrap: wrap; } }

	&__item {
		position: relative;
		flex: 0 0 20%;
		height: 318px;
		background: $white;
		border: 1px solid #eeeeee;
		border-right: 0;
		@include md {
			height: 260px; }
		@include sm {
			flex: 1 1 50%;
			min-width: 250px; }
		&:last-child {
			border-right: 1px solid #eeeeee; } }
	&__item:hover &__inner {
		height: 372px;
		width: 245px;
		padding: 42px 22px 42px 30px;
		top: 50%;
		transform: translate(-10px ,-50%);
		transition: height .3s, width .3s;
		background-color: $aqua-dark;
		z-index: 7;
		box-shadow: 2px 3px 2px rgba(0, 0, 0, 0.1);
		@include lg {
			height: 330px;
			padding: 23px 22px 42px 30px; }
		@include sm {
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			transform: none;
			height: auto;
			width: auto;
			padding: 20px 22px 42px 30px; } }
	&__item:first-child:hover &__inner {
		@include lg {
			transform: translate(0, -50%); }
		@include sm {
			transform: none; } }

	&__item:last-child:hover &__inner {
		@include lg {
			transform: translate(0,-50%);
			right: -1px;
			left: auto; }
		@include sm {
			transform: none; } }

	&__item:hover &__title {
		text-align: left;
		color: $white;
		@include f('pn-600', 18);
		line-height: 23px; }
	&__item:hover &__descr {
		color: $white; }
	&__item:hover &__descr {
			visibility: visible;
			opacity: 1; }
	&__item:last-child &__title {
		margin: 0 -10px 20px;
		@include md {
			margin: 0 0 20px; } }
	&__inner {
		position: absolute;
		right: -1px;
		left: -1px;
		height: 318px;
		z-index: 6;
		padding: 62px 30px 30px;
		@include md {
			height: 260px;
			padding: 30px 5px 10px; } }

	&__title {
		position: relative;
		display: block;
		margin: 0 0 20px;
		font-size: 16px;
		line-height: 21px;
		color: $black;
		text-align: center;
		@include md {
			font-size: 15px; }
		@include sm {
			margin: 0 0 10px;
			font-size: 17px; } }
	&__descr {
		display: block;
		visibility: hidden;
		opacity: 0;
		transition: opacity .8s;
		font-size: 14px;
		line-height: 18px; }
	&__img {
		position: absolute;
		width: 100%;
		left: 0;
		bottom: 60px;
		font-size: 97px;
		fill: $blue-dark;
		@include md {
			bottom: 40px;
			font-size: 78px; }
		@include sm {
			font-size: 100px; }
		&.icon-services-5 {
			stroke: #fff;
			stroke-width: 1px; } }

	&_full {
		padding: 70px 0 80px; } }
