.designing {
	padding: 69px 0;
	background-repeat: no-repeat;
	background-position: top 10px right;
	@include r(1599) {
		background-size: 61%; }
	@include sm {
		padding: 90px 0 60px;
		background-size: 320px; }
	&__top-info {
		max-width: 400px;
		margin-bottom: 47px;
		@include sm {
			margin-bottom: 25px; } }
	&__title {
		margin-bottom: 18px;
		@include sm {
			font-size: 31px; } }
	&__text {
		@include f('pn-300', 17);
		line-height: 32px;
		color: #585858;
		p {} }
	&__block {
		&-title {
			margin-bottom: 17px;
			@include f('pn-600', 17); }
		&-list {
			li {
				position: relative;
				padding-left: 20px;
				font-style: italic;
				svg {
					position: absolute;
					left: 0;
					top: 10px;
					fill: #2ab1b4;
					font-size: 11px; } } }
		&:first-of-type {
			margin-bottom: 17px; } } }
