.our {
	&__container {
		display: flex;
		justify-content: space-between;
		flex-wrap: wrap;
		max-width: 1190px;
		@include sm {
			justify-content: center; } }
	&__block {
		flex: 0 1 560px;
		min-width: 1px;
		@include lg {
			flex: 0 1 460px; }
		@include md {
			flex: 0 1 360px; }
		@include sm {
			flex: 0 1 460px;
			&:not(:first-child) {
				margin-top: 100px; } } }
	&__block:nth-child(2) &__title {
		margin: 0 0 25px 27px;
		@include md {
			margin: 0 auto 20px; }
		@include sm {
			margin: 0 auto 10px; } }
	&__block:nth-child(2) .slider-btn_prev {
		right: 220px;
		@include lg {
			right: 41px; }
		@include md {
			right: calc(50% -  -2px); } }
	&__block:nth-child(2) .slider-btn_next {
		right: 179px;
		@include lg {
			right: 0; }
		@include md {
			right: calc(50% - 40px); } }
	&__title {
		max-width: 270px;
		margin: 0 0 25px 22px;
		@include md {
			text-align: center;
			margin: 0 auto 20px; }
		@include sm {
			margin: 0 auto; } }
	&__slider {
		display: none;
		background: url("../img/bg/our-block_bg.png") no-repeat 50% 50%;
		@include md {
			background: 0 0; }
		&.slick-initialized {
			display: block; }
		.slider-btn {
			top: -65px;
			@include md {
				top: 105%; }
			&_prev {
				right: 206px;
				@include lg {
					right: 41px; }
				@include md {
					right: calc(50% -  -2px); } }
			&_next {
				right: 165px;
				@include lg {
					right: 0; }
				@include md {
					right: calc(50% - 40px); } } } }
	&__item {
		&-inner {
			display: flex;
			flex-direction: column;
			justify-content: center;
			align-items: center;
			height: 80px; }
		&-client {
			position: relative;
			.no-touch &:hover .our__item-img {
				opacity: 0; }
			.no-touch &:hover .our__item-img_hidden {
				opacity: 1; } }
		&-img {
			transition: opacity .3s ease-in-out;
			&_hidden {
				@include coverdiv;
				opacity: 0;
				background-size: contain;
				background: no-repeat 50% 50%; } } }
	&__btn {
		margin: 45px auto 0;
		padding: 10px 43px;
		@include md {
			margin: 85px auto 0; } }

	&_default {
		padding: 93px 0 90px;
		@include md {
			padding: 70px 0 70px; }
		@include sm {
			padding: 50px 0; } } }

/* ===================================
	 clients
	 @mixin ================================== */
.clients {
	padding: 73px 0 77px;
	@include md {
		padding: 60px 0; }
	&__container {
		text-align: center; }
	&__block {
		display: flex;
		flex-wrap: wrap;
		justify-content: center;
		margin: 20px 0 0;
		padding: 10px 0;
		background: url("../img/bg/clients_bg.png") no-repeat 50% 50%;
		@include lg {
			background-size: 100%; }
		@include md {
			padding: 0 20px;
			background: 0 0; }
		.slider-btn {
			&_prev {
				left: 0; }
			&_next {
				right: 0; } } }
	&__item {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-content: center;
		align-items: center;
		flex: 1 1 (100%/6);
		height: 80px;
		@include md {
			min-width: 185px;
			@include vertical; }
		&-link {
			position: relative;
			display: flex;
			justify-content: center;
			flex: 0 1 auto;
			@include md {
				display: inline-block;
				vertical-align: middle; }
			.no-touch &:hover .clients__item-img {
				opacity: 0; }
			.no-touch &:hover .clients__item-img_hidden {
				opacity: 1; } }
		&-img {
			transition: opacity .3s ease-in-out;
			img {
				display: block; }
			&_hidden {
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				opacity: 0;
				background-size: contain;
				background: no-repeat 50% 50%; } } } }
