.subscribe {
	padding: 48px 0 45px;
	background: url("../img/bg/subscribe_bg.png") repeat-x 50% 50%/cover;
	@include md {
		padding: 30px 0 40px; }
	&__container {
		display: flex;
		align-items: center;
		@include md {
			flex-wrap: wrap; } }
	&__title {
		margin-right: 30px;
		@include md {
			margin: auto; }
		@include sm {
			font-size: 23px; } }
	&__descr {
		flex: 0 0 265px;
		margin: 0 25px;
		color: $white;
		line-height: 20px;
		@include md {
			flex: 0 1 420px; }
		@include sm {
			margin: 10px 25px 0; } }
	&__form {
		flex: 1 1 auto;
		@include md {
			margin: 20px 0 0; }
		@include sm {
			margin: 5px 0 0; }
		&-in {
			display: flex;
			@include sm {
				flex-wrap: wrap;
				align-items: center; } }
		&-field {
			flex: 1 1 auto;
			margin-right: 10px;
			@include sm {
				flex: 1 1 auto;
				min-width: 300px;
				margin: 15px 0; } }
		&-btn {
			padding: 11px 39px;
			@include sm {
				flex: 1 1 auto; } } }

	&_has-title {
		padding: 51px 0 49px; } }

