/* ===================================
	 contacts
	 @mixin ================================== */
.contacts {
	padding: 72px 0 80px;
	@include sm {
		padding: 50px 0; }
	&__container {
		display: flex;
		align-items: flex-start;
		@include md {
			flex-wrap: wrap;
			justify-content: center; } }
	&__info {
		flex: 1 1 auto;
		padding-right: 10px;
		text-align: center;
		@include md {
			padding-right: 0; }
		&-btn {
			padding: 10px 37px; } }
	&__data {
		display: flex;
		@include md {
			flex-wrap: wrap; }
		&-item {
			flex: 1 1 33.33333%;
			padding: 0 8px;
			text-align: center;
			min-width: 246px;
			@include md {
				margin-bottom: 20px; } }

		&-icon {
			position: relative;
			display: flex;
			justify-content: center;
			align-items: center;
			margin-bottom: 10px;
			height: 68px;
			&::after {
				@include vcenter;
				display: block;
				margin: 0 auto;
				content: '';
				@include s('logo-circle_lg');
				opacity: .3; }
			svg {
				fill: #2ab1b4; }
			.icon-tel {
				font-size: 28px; }
			.icon-balloon {
				font-size: 30px; }
			.icon-mail {
				font-size: 20px; } }
		&-link {
			display: block;
			font-size: 22px;
			line-height: 25px;
			@include lg {
				font-size: 19px; } }

		&-descr {
			font-size: 16px;
			color: $gray; } }
	&__form {
		flex: 0 0 260px;
		text-align: center;
		@include md {
			flex: 0 1 460px;
			padding-top: 50px; }
		&-field {
			&:not(:first-child) {
				margin-top: 10px; } }
		&-btn {
			padding: 11px 40px;
			margin-top: 30px; } }
	&__textarea {
		font-size: 15px;
		@include placeholder {
			font-size: 15px; } }
	&__title {
		margin-bottom: 33px;
		text-align: left;
		@include md {
			text-align: center; } }

	&__team {
		display: flex;
		padding: 50px 0 37px;
		@include md {
			flex-wrap: wrap;
			padding: 20px 0 37px; }
		.team__person {
			min-width: 246px;
			margin: 0;
			@include md {
				&:not(:last-child) {
					margin-bottom: 30px; } } }

		.team__person-title {
			font-size: 20px; } } }

.contact-block {
	display: flex;
	align-items: center;
	text-align: center;
	@include cover;
	&__container {
		z-index: 2; }
	&__phone {
		margin-bottom: 10px;
		@include sm {
			font-size: 32px; } }
	&__email {
		font-size: 35px;
		line-height: 35px;
		color: $white;
		border-bottom: 1px solid #cfebee;
		@include tr(border-color .3s);
		@include sm {
			font-size: 26px; }
		@include hover {
			border-bottom-color: transparent; } }
	&__address {
		margin: 15px 0 33px;
		font-style: normal;
		font-size: 20px;
		line-height: 25px;
		color: $white;
		text-align: center;
		@include sm {
			align-self: stretch; } }
	&__actions {
		margin: 0 -20px;
		@include sm {
			display: flex;
			flex-direction: column; }
		&-btn {
			margin:  0 10px;
			padding: 11px 39px;
			@include sm {
				margin: 10px; } } }

	&__form {
		max-width: 795px;
		margin: 30px auto 0;
		text-align: center; }
	&__form-row {
		display: flex;
		@include sm {
			flex-wrap: wrap; }
		& + & {
			margin-top: 20px; } }
	&__form-field {
		flex: 1 1 255px;
		margin: 0 5px;
		@include sm {
			flex: 1 1 100%;
			&:not(:first-child) {
				margin-top: 15px; } } }

	&__form-descr {
		margin: 15px 0 7px;
		color: $white;
		font-size: 15px;
		line-height: 25px; }
	&__input {
		padding: 0 23px;
		@include f('pn-300', 16); }
	&__textarea {
		min-height: 100px;
		margin: 0 5px; }
	&_has-address {
		padding: 80px 0 90px;
		@include sm {
			padding: 60px 0; } }
	&_has-address &__container {
		display: flex;
		flex-direction: column;
		align-items: center;
		flex: 1 1 100%;
		max-width: 550px; }

	&_has-form {
		padding: 81px 0;
		@include sm {
			padding: 40px 0; } }
	&_has-form .title-sm-white {
		@include sm {
			font-size: 23px; } }
	&_has-form &__container {
		flex: 1 1 auto; }
	&_has-form-mod {
		padding: 83px 0 81px; }
	&_has-form-mod &__container {
		flex: 1 1 auto; }
	&_has-form-mod .contact-block__form-row + .contact-block__form-row {
		margin-top: 16px; }

	&_partners {
		position: relative;
		padding: 72px 0 81px;
		background: #f2fafd;
		@include sm {
			padding: 50px 0 40px; } }
	&_partners .contact-block__form {
		@include sm {
			text-align: center; } }
	&_partners .title-lg {
		margin-bottom: 8px;
		color: $blue-dark;
		font-size: 40px;
		text-shadow: none; }
	&_partners .title-sm-white {
		text-transform: none;
		font-size: 25px;
		@include sm {
			font-size: 22px;
			line-height: 24px; } }
	&_partners &__input {
		@include f('pn-400'); }
	&_partners &__textarea {
		min-height: 136px;
		padding: 12px 24px;
		@include placeholder {
			font-size: 16px; } }
	&_partners &__form {
		text-align: left; }
	&_partners &__form-btn {
		margin: 0 5px; }
	&_partners &__form-row + &__form-row {
		margin-top: 10px; }
	&_partners &__form-descr {
		margin: 15px 5px 13px;
		text-align: left;
		color: $gray;
		@include sm {
			text-align: center; } }
	&_partners .btn_file {
		padding: 10px 20px 10px 52px;
		border: 0;
		background: transparent;
		.no-touch &:hover svg {
			fill: $blue-dark; }
		@include hover {
			background: transparent;
			color: $blue-dark; } } }

