.vacancy {
	padding: 70px 0;
	@include md {
		padding: 50px 0; }
	@include sm {
		padding: 50px 0 10px; }
	&__item {}
	&__title {
		margin-bottom: 6px;
		font-size: 35px; }
	&__descr {
		font-size: 22px;
		line-height: 25px;
		color: #00899a;
		@include sm {
			font-size: 19px; } }
	&__details {
		padding-top: 45px;
		display: flex;
		@include md {
			flex-wrap: wrap; }
		@include sm {
			padding-top: 20px; } }
	&__block {
		@include md {
			flex: 1 1 50%;
			&:nth-child(3) {
				padding: 20px 0 0;
				flex: 1 1 100%;
				text-align: right; } }
		@include sm {
			flex: 1 1 100%;
			&:not(:first-child) {
				margin-top: 20px; } }
		&:not(:last-child) {
			padding-right: 52px;
			@include lg {
				padding-right: 25px; }
			@include sm {
				padding-right: 0; } }
		&-title {
			margin-bottom: 6px;
			font-size: 22px;
			line-height: 25px; }
		&-list {
			font-size: 17px;
			line-height: 35px;
			color: $gray;
			li {
				position: relative;
				padding-left: 20px;
				@include sm {
					line-height: 25px; }
				.icon-arr {
					position: absolute;
					top: 12px;
					left: 2px;
					fill: #2ab1b4;
					font-size: 12px;
					@include gpu;
					@include sm {
						top: 7px; } } } }
		&-salary {
			margin-top: 18px;
			font-size: 20px;
			&-count {
				display: flex;
				align-items: center;
				@include f('pn-900');
				text-transform: uppercase;
				color: #78c698;
				@include md {
					display: inline-block; }
				span {
					margin: 0 13px;
					font-size: 40px;
					letter-spacing: 0.04em; } }
			&-descr {
				font-size: 17px;
				color: $gray; } } }
	&__actions {
		display: flex;
		justify-content: space-between;
		margin: 30px 0 0;
		@include sm {
			flex-direction: column; }
		&-submit {
			@include sm {
				margin-bottom: 10px; } }
		&-toggling {
			position: relative;
			min-width: 184px;
			padding: 10px 30px 10px 55px;
			&.is-hidden svg {
				@include rotate(90); }
			.no-touch &:hover svg {
				fill: $white; }
			svg {
				position: absolute;
				left: 42px;
				top: calc(50% - 6px);
				font-size: 12px;
				fill: $blue-dark;
				@include rotate(-90);
				transition: .3s ease-in-out; } } } }


/* ===================================
	 vac-links
	 @mixin ================================== */
.vac-links {
	padding: 30px 0 90px;
	@include sm {
		padding: 30px 0 50px; }
	&__container {}
	&__item {
		display: flex;
		align-items: center;
		justify-content: space-between;
		@include sm {
			flex-direction: column; }
		&:not(:first-child) {
			margin-top: 65px;
			@include sm {
				margin-top: 25px; } }
		&-col {
			flex: 1 1 50%;
			&:nth-child(2) {
				padding-left: 15px; }
			@include sm {
				flex: 1 1 100%;
				width: 100%;
				text-align: center; } } }
	&__link {
		font-size: 35px;
		border-bottom: 1px solid currentColor;
		transition: .3s;
		@include md {
			font-size: 24px; }
		@include sm {
			font-size: 19px; }
		.no-touch &:hover {
			border-bottom-color: transparent; } }
	&__descr {
		font-size: 22px;
		@include lg {
			text-align: right; }
		@include md {
			font-size: 20px; }
		@include sm {
			text-align: center;
			font-size: 17px; } } }
