.popup {
	position: fixed;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	opacity: 0;
	-ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=0)";
	visibility: hidden;
	z-index: 1050;
	overflow: auto;
	outline: 0;
	text-align: center;
	font-size: 0;
	transform: translate3d(0, 0, 0);
	background-color: rgba(0, 0, 0, 0.7);
	-webkit-transition: opacity .3s ease, -webkit-transform .5s ease;
	transition: opacity .3s ease, -webkit-transform .5s ease;
	-o-transition: opacity .3s ease, -o-transform .5s ease;
	-moz-transition: opacity .3s ease, transform .5s ease, -moz-transform .5s ease;
	transition: opacity .3s ease, transform .5s ease;
	transition: opacity .3s ease, transform .5s ease, -webkit-transform .5s ease, -moz-transform .5s ease, -o-transform .5s ease;
	will-change: opacity, transform;
	&:after {
		content: '';
		display: inline-block;
		vertical-align: middle;
		width: 0;
		height: 100%; }
	&.opened {
		opacity: 1;
		-ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=100)";
		visibility: visible; }
	&__close {
		position: absolute;
		top: 24px;
		right: 29px;
		width: 13px;
		height: 13px;
		cursor: pointer;
		@include sm {
			right: 15px;
			top: 15px; }
		svg {
			font-size: 13px; }
		span {
			display: block;
			width: 100%;
			height: 1px;
			background-color: #d0d0d0;
			&:first-of-type {
				-webkit-transform: rotate(45deg);
				-moz-transform: rotate(45deg);
				-ms-transform: rotate(45deg);
				-o-transform: rotate(45deg);
				transform: rotate(45deg); }
			&:last-of-type {
				-webkit-transform: rotate(-45deg) translateY(-1px);
				-moz-transform: rotate(-45deg) translateY(-1px);
				-ms-transform: rotate(-45deg) translateY(-1px);
				-o-transform: rotate(-45deg) translateY(-1px);
				transform: rotate(-45deg) translateY(-1px); } } }
	&__container {
		display: inline-block;
		vertical-align: middle;
		width: 100%;
		max-width: 618px;
		position: relative;
		padding: 50px;
		background: $white;
		box-shadow: 2px 3px 2px rgba(0, 0, 0, 0.1);
		border-radius: 20px;
		border: 1px solid #eeeeee;
		-webkit-box-shadow: 0 3px 24px rgba(0, 0, 0, 0.12);
		-moz-box-shadow: 0 3px 24px rgba(0, 0, 0, 0.12);
		margin: 30px auto;
		-webkit-transition: opacity .2s ease, visibility 0s .1s, -webkit-transform .3s ease;
		transition: opacity .2s ease, visibility 0s .1s, -webkit-transform .3s ease;
		-o-transition: opacity .2s ease, visibility 0s .1s, -o-transform .3s ease;
		-moz-transition: opacity .2s ease, transform .3s ease, visibility 0s .1s, -moz-transform .3s ease;
		transition: opacity .2s ease, transform .3s ease, visibility 0s .1s;
		transition: opacity .2s ease, transform .3s ease, visibility 0s .1s, -webkit-transform .3s ease, -moz-transform .3s ease, -o-transform .3s ease;
		-webkit-transform: translate3d(0, -50px, 0);
		-moz-transform: translate3d(0, -50px, 0);
		transform: translate3d(0, -50px, 0);
		will-change: transform, opacity;
		@include sm {
			margin: 15px 0;
			padding: 15px; } }

	&__top {
		display: flex;
		justify-content: space-between;
		max-width: 440px;
		margin: 0 auto 33px;
		@include sm {
			max-width: 220px;
			margin: 0 auto 15px; }
		&-icon {
			position: relative;
			width: 68px;
			height: 68px;
			line-height: 98px;
			&:nth-child(3) {
				line-height: 90px; }
			&::before {
				content: '';
				position: absolute;
				top: 0;
				left: 0;
				@include s('logo-circle_lg');
				opacity: .3; }
			svg {
				fill: #2ab1b4; }
			.icon-tel {
				font-size: 29px; }
			.icon-balloon {
				font-size: 30px; }
			.icon-mail {
				font-size: 20px; }
			&_circle {
				border-radius: 50%;
				background: #2ab1b4;
				svg {
					fill: $white; }
				&::before {
					display: none; } } } }
	&__text {
		max-width: 425px;
		margin: 0 auto 30px; }
	&__title {
		font-size: 35px;
		margin-bottom: 8px;
		&-link {
			color: #2ab1b4;
			display: inline-block;
			text-decoration: underline; } }
	&__descr {
		font-size: 20px;
		color: $blue-dark;
		@include f('pn-700');
		letter-spacing: .025em;
		@include sm {
			line-height: 24px; } }
	&__tel {
		display: block;
		margin-top: 29px;
		font-size: 35px;
		@include sm {
			font-size: 32px; } }
	&__form {
		&-row {
			display: flex;
			margin: 0 -5px;
			@include sm {
				flex-wrap: wrap; }
			&:not(:first-child) {
				margin-top: 10px; }
			&_full {
				margin: 0;
				@include sm {
					margin: 0 -5px; } } }
		&-field {
			flex: 0 1 50%;
			margin: 0 5px;
			@include sm {
				flex: 1 1 100%;
				margin: 0;
				&:not(:first-child) {
					margin-top: 10px; } }
			input {
				@include sm {
					padding: 0 20px; } }
			&_date {
				position: relative;
				font-size: 16px;
				input {
					padding: 0 40px 0 30px; }
				.icon-date {
					position: absolute;
					top: 14px;
					right: 20px;
					pointer-events: none;
					@include sm {
						display: none; } } }
			&_full {
				flex: 1 1 100%; } }
		&-descr {
			margin: 13px -4px 0;
			font-size: 15px;
			color: #585858;
			line-height: 25px;
			text-align: left;
			-ms-text-align-last: center;
			text-align-last: center; }
		&-example {
			padding-left: 20px;
			@include f('pn-300', 13);
			color: #9a9a9a;
			text-align: left;
			&_hidden-mob {
				@include sm {
					display: none; } }
			&_visible-mob {
				@include sm {
					display: block; } } }
		&-actions {
			margin-top: 10px;
			padding-top: 10px;
			border-top: 1px solid #f0f1f3;
			&-in {
				display: flex;
				justify-content: space-between;
				@include sm {
					flex-direction: column; } } } }
	&__submit-btn {
		margin-top: 11px; }

	&_call &__text {
		margin-bottom: 0; }
	&_vacancy &__container {
		@include sm {
			padding-top: 25px; } }
	&_vacancy &__text {
		margin: 8px auto 30px; }
	&_vacancy &__title {
		text-transform: none;
		font-size: 30px; }
	&_vacancy &__form-descr {
		margin-top: 0;
		margin-bottom: 10px; }
	&_vacancy &__submit-btn {
		margin-top: 0;
		@include sm {
			margin-top: 10px; } } }

body.popup-opened {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	overflow: hidden; }

.popup.opened .popup__container {
	-webkit-transform: translate3d(0, 0, 0);
	-moz-transform: translate3d(0, 0, 0);
	transform: translate3d(0, 0, 0); }
