@media only screen and (max-width: 1023px) { .info__tags-list li { margin: 0 15px 10px; } }

@media only screen and (max-width: 767px) { .info__tags-list li { margin: 0 10px 10px; } }

.info__tags-list li a { font-family: "ProximaNova-Bold", Arial, sans-serif; font-weight: normal; font-size: 20px; text-transform: uppercase; color: #0f2740; transition: .3s; }

@media only screen and (max-width: 767px) { .info__tags-list li a { font-size: 16px; } }

.no-touch .info__tags-list li a:hover { color: #2ab1b4; }

.no-touch .info__tags-list li a:hover span { color: #0f2740; }

.info__tags-list li a span { color: #2ab1b4; transition: .3s; }

.blog { padding: 50px 0; font-size: 0; }

@media only screen and (max-width: 1023px) { .blog { padding: 50px 0 20px; } }

.blog__inner { text-align: center; *zoom: 1; }

.blog__inner:after { content: " "; display: table; clear: both; }

@media only screen and (max-width: 1023px) { .blog__inner { display: flex; flex-wrap: wrap; margin: 0 -10px; } }

.blog__item { float: left; position: relative; margin-right: 31px; width: 22.88288%; margin-bottom: 30px; text-align: left; }

@media only screen and (max-width: 1200px) { .blog__item { width: 25%; margin-bottom: 15px; margin-right: 14px; } }

@media only screen and (max-width: 1023px) { .blog__item { flex: 1 1 auto; margin: 0 5px 15px; } }

@media only screen and (max-width: 767px) { .blog__item { flex: 1 1 100%; } }

.blog__item.is-hidden { display: none; }

.blog__item:nth-child(3n) { margin-right: 0; }

@media only screen and (max-width: 1023px) { .blog__item:nth-child(3n) { margin: 0 5px 15px; } }

.blog__item_md { margin-right: 30px; width: 48.64865%; }

@media only screen and (max-width: 1200px) { .blog__item_md { width: 47%; margin-right: 15px; } }

@media only screen and (max-width: 1023px) { .blog__item_md { margin: 0 5px 15px; } }

@media only screen and (max-width: 1023px) { .blog__item_md.is-hidden { flex: 1 1 100%; } }

.blog__item_md .blog__item-text { align-items: flex-start; flex-wrap: wrap; flex-direction: row; padding: 14px 18px 17px; background: rgba(255, 255, 255, 0.9); border: 2px solid #e0e0e0; min-height: 0; }

.blog__item_md .blog__item-row { margin-top: 2px; align-items: center; }

.blog__item_md .blog__item-title { margin: 0 7px 0 0; }

.blog__item_md .blog__item-date { line-height: 25px; }

.blog__item_md .blog__item-descr { padding-right: 2px; height: 85px; margin: 0; }

.blog__item_md .blog__item-img { width: 100%; }

.blog__item_md .blog__item-img-wrap { height: 241px; }

@media only screen and (max-width: 767px) { .blog__item_md .blog__item-img-wrap { height: auto; } }

.blog__item-img-wrap { height: 153px; overflow: hidden; border-radius: 3px 3px 0 0; }

@media only screen and (max-width: 767px) { .blog__item-img-wrap { height: auto; } }

.blog__item-text { display: flex; flex-direction: column; padding: 16px 18px 13px; border: 2px solid #e0e0e0; border-top: 0; min-height: 212px; }

.blog__item-title { margin-bottom: 2px; font-size: 18px; line-height: 25px; }

.blog__item-descr { height: 140px; margin-bottom: 7px; color: #000; opacity: .8; font-family: "ProximaNova-Light", Arial, sans-serif; font-weight: normal; font-size: 16px; line-height: 28px; }

@media only screen and (max-width: 767px) { .blog__item-descr { height: auto; } }

.blog__item-btn { margin: 13px 0 0; padding: 5px 25px; }

.blog__item-row { display: flex; justify-content: space-between; }

.blog__item-date { font-size: 16px; color: #2ab1b4; }

.blog__item-link { text-transform: uppercase; font-family: "ProximaNova-Bold", Arial, sans-serif; font-weight: normal; font-size: 14px; text-decoration: underline; }

.blog__more-btn { margin-top: 10px; }

@media only screen and (max-width: 1023px) { .blog__more-btn { align-self: flex-end; flex: 1 1 100%; } }

.nav-toggle { display: none; position: absolute; right: 15px; top: 3px; z-index: 300; }

.line { width: 48px; height: 4px; background-color: #00899a; margin: 8px auto; -webkit-transition: all 0.3s ease-in-out; -o-transition: all 0.3s ease-in-out; transition: all 0.3s ease-in-out; }

.nav-toggle.is-active { -webkit-transition: all 0.3s ease-in-out; -o-transition: all 0.3s ease-in-out; transition: all 0.3s ease-in-out; -webkit-transition-delay: 0.5s; -o-transition-delay: 0.5s; transition-delay: 0.5s; -webkit-transform: rotate(45deg); -ms-transform: rotate(45deg); -o-transform: rotate(45deg); transform: rotate(45deg); }

.nav-toggle.is-active .line:nth-child(2) { width: 0; }

.nav-toggle.is-active .line { background-color: #fff; }

.nav-toggle.is-active .line:nth-child(1), .nav-toggle.is-active .line:nth-child(3) { -webkit-transition-delay: 0.3s; -o-transition-delay: 0.3s; transition-delay: 0.3s; }

.nav-toggle.is-active .line:nth-child(1) { -webkit-transform: translateY(12px); -ms-transform: translateY(12px); -o-transform: translateY(12px); transform: translateY(12px); }

.nav-toggle.is-active .line:nth-child(3) { -webkit-transform: translateY(-12px) rotate(90deg); -ms-transform: translateY(-12px) rotate(90deg); -o-transform: translateY(-12px) rotate(90deg); transform: translateY(-12px) rotate(90deg); }
