.info {
	padding: 70px 0 30px;
	@include lg {
		padding: 50px 0 30px; }
	&__tags {
		max-width: 1010px;
		margin: 0 auto 50px;
		@include lg {
			margin: 0 auto; }
		&-list {
			display: flex;
			justify-content: center;
			flex-wrap: wrap;
			li {
				margin: 0 26px 12px;
				@include md {
					margin: 0 15px 10px; }
				@include sm {
					margin: 0 10px 10px; }
				a {
					@include f('pn-700', 20);
					text-transform: uppercase;
					color: $blue-dark;
					transition: .3s;
					@include sm {
						font-size: 16px; }
					@include hover {
						color: #2ab1b4; }
					.no-touch &:hover span {
						color: $blue-dark; }
					span {
						color: #2ab1b4;
						transition: .3s; } } } } } }

.blog {
	padding: 50px 0;
	font-size: 0;
	@include md {
		padding: 50px 0 20px; }
	&__inner {
		text-align: center;
		@include clr;
		@include md {
			display: flex;
			flex-wrap: wrap;
			margin: 0 -10px; } }
	&__item {
		float: left;
		position: relative;
		margin-right: 31px;
		width: percentage(254 / 1110);
		margin-bottom: 30px;
		text-align: left;
		@include lg {
			width: 25%;
			margin-bottom: 15px;
			margin-right: 14px; }
		@include md {
			flex: 1 1 auto;
			margin: 0 5px 15px; }
		@include sm {
			flex: 1 1 100%; }
		&.is-hidden {
			display: none; }
		&:nth-child(3n) {
			margin-right: 0;
			@include md {
				margin: 0 5px 15px; } }
		&_md {
			margin-right: 30px;
			width: percentage(540 / 1110);
			@include lg {
				width: 47%;
				margin-right: 15px; }
			@include md {
				margin: 0 5px 15px; }
			&.is-hidden {
				@include md {
				    flex: 1 1 100%; } } }
		&_md &-text {
			align-items: flex-start;
			flex-wrap: wrap;
			flex-direction: row;
			padding: 14px 18px 17px;
			background: rgba(#fff, .9);
			border: 2px solid #e0e0e0;
			min-height: 0; }
		&_md &-row {
			margin-top: 2px;
			align-items: center; }
		&_md &-title {
			margin: 0 7px 0 0; }
		&_md &-date {
			line-height: 25px; }
		&_md &-descr {
			padding-right: 2px;
			height: 85px;
			margin: 0; }
		&_md &-img {
			width: 100%; }
		&_md &-img-wrap {
			height: 241px;
			@include sm {
				height: auto; } }
		&-img-wrap {
			height: 153px;
			overflow: hidden;
			border-radius: 3px 3px 0 0;
			@include sm {
				height: auto; } }
		&-img {}
		&-text {
			display: flex;
			flex-direction: column;
			padding: 16px 18px 13px;
			border: 2px solid #e0e0e0;
			border-top: 0;
			min-height: 212px; }
		&-title {
			margin-bottom: 2px;
			font-size: 18px;
			line-height: 25px; }
		&-descr {
			height: 140px;
			margin-bottom: 7px;
			color: #000;
			opacity: .8;
			@include f('pn-300', 16);
			line-height: 28px;
			@include sm {
				height: auto; } }
		&-btn {
			margin: 13px 0 0;
			padding: 5px 25px; }
		&-row {
			display: flex;
			justify-content: space-between; }
		&-date {
			font-size: 16px;
			color: #2ab1b4; }
		&-link {
			text-transform: uppercase;
			@include f('pn-700', 14);
			text-decoration: underline; } }

	&__more-btn {
		margin-top: 10px;
		@include md {
			align-self: flex-end;
			flex: 1 1 100%; } } }
