.partners-block {
	&__container {}
	&__title {
		text-align: center; }
	&__row {
		display: flex;
		max-width: 820px;
		margin: 30px auto 0;
		padding: 35px 0 35px 65px;
		background: url(../img/bg/partners_bg.png) no-repeat 100% 50%;
		@include md {
			padding: 35px 0; }
		@include sm {
			align-items: center;
			flex-direction: column;
			margin: 0 auto;
			background-position: 43% 60%; } }
	&__item {
		position: relative;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		flex: 1 1 auto;
		@include sm {
			flex: 0 1 auto;
			background: $white;
			margin: 0 30px;
			&:not(:first-child) {
				margin-top: 30px; } }
		&-link {
			position: relative;
			.no-touch &:hover .partners-block__item-img {
				opacity: 0; }
			.no-touch &:hover .partners-block__item-img_color {
				opacity: 1; } }
		&-img {
			transition: opacity .3s ease-in-out; }
		&-img_color {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			opacity: 0;
			background-size: contain;
			background: no-repeat 50% 50%; } }
	&_default {
		padding: 110px 0 88px;
		@include md {
			padding: 60px 0 70px; }
		@include sm {
			padding: 50px 0 20px; } } }
